@media (max-width : 991px) {
  #header {
    background: url('../Assets/homebanner2.jpg') !important;
    background-size: cover !important;
    background-repeat: no-repeat !important;
    /* height: 80vh; */

  }

  #header .logo img {
    max-height: 50px !important;
  }

  .banner-end h3 {
    font-size: xx-large;
    color: #fff;
  }

