    margin: 20px;

  }
}
.banner-end {
  margin-top: -80px;
  color: #fff;
}

.banner-end p {
  opacity: 0.8;
  font-size: 12px;
}
/* Navbar.css */
.fixed-top {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.13) !important;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
  transition: top 0.9s ease-in-out, background-color 0.5s ease-in-out !important; /* Add transition for top and background-color */
}
.bg-white {
  background-color: white;
  color: #000 !important;
  transition-delay: 0.2s; /* Add a delay to the transition */
}
.hide-extras {
  display: none;
}

/* Style for the navbar brand when it has a white background */
.fixed-top .navbar-brand {
  color: black !important;
}

/* Additional styles for the navbar brand, adjust as needed */
.fixed-top .navbar-brand img {
  max-height: 50px !important; /* Set the maximum height for the logo */
  /* Add more styles as needed */
}

#header {
  background: url('../Assets/homebanner2.jpg');
  background-size: cover !important;
  background-repeat: no-repeat !important;
  /* font-family: "Poppins" sans-serif !important; */
  transition: all 0.5s;
  z-index: 997;
  height: 80vh;
}

#header .logo a {
  color: #026fe0;
}

.logo img {
  max-height: 60px !important;
}

.navbar {
  box-shadow: none;
}

.admission-button {
  padding-top: 6px;
  margin-right: 180px;
}

.admission-button1 {
  padding-top: 20px;
  /* margin-right: 35px; */
}
.btn1 {
  height: auto;
  border-radius: 50px;
  padding: 10px 70px;
  background-color: #FFD92A;
  border: 0;
  box-shadow: none;
  font-weight: 600;
  font-size: 14px;
}

.btn {
  height: auto;
  border-radius: 50px;
  padding: 7px 30px;
  background-color: #FFD92A;
  border: 0;
  box-shadow: none;
  font-weight: 700;
  font-size: 12px;
}
.btn,.btn1:hover{
  background-color: #FFD92A !important;
}

.latest-div {
  background: #D00846;
}

.latest-div ul {
  font-size: smaller;
  padding-top: 10px;
  margin-left: 150px;
}

.latest-div ul li {
  padding-left: 20px;
  font-weight: 500;
}

.navbar a {
  font-size: 20px;
  font-weight: 600;
  color: #fff;
  text-decoration: none;
}

.extras-nav {
  margin-top: 10px;
}

.extras-nav a {
  font-size: 14px;
  opacity: 0.8;
}

.navActive a {
  text-decoration: none !important;
  color: rgb(16, 11, 123) !important;
}

.offcanvas-header {
  padding: 25px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.navbar-toggler {
  color: #fff;
  margin-bottom: 20px;
  padding: 20px;
  /* font-size: 50px !important; */
  /* border-color: 1px #fff solid !important; */
}

/* Color of the links BEFORE scroll */
.navbar-scroll .nav-link,
.navbar-scroll .navbar-toggler-icon,
.navbar-scroll {
  color: #fff;
}

/* Color of the links AFTER scroll */
.navbar-scrolled .nav-link,
.navbar-scrolled .navbar-toggler-icon,
.navbar-scrolled {
  color: #fff;
}

/* Color of the navbar AFTER scroll */
.navbar-scroll,
.navbar-scrolled {
  background-color: #cbbcb1;
}

.mask-custom {
  backdrop-filter: blur(5px);
  background-color: rgba(255, 255, 255, .15);
}

.navbar-brand {
  font-size: 1.75rem;
  letter-spacing: 3px;
}